<template>
    <header class="naviHeader">
        <button class="home"
                @click="toHome">
            <i class="icon iconfont icon-fire"></i>
        </button>
        <div class="user">
            <span>
                {{time}}{{userName}}
            </span>
            <button @click="logout">
                <span></span>
                <i class="icon iconfont icon-signout"></i>
            </button>
        </div>
    </header>
</template>
<script>
    import {get, unset}      from '../js/cookieUtil'
    export default{
        data(){
            return {
                userName: ''
            }
        },
        created(){
            this.userName = get('user')
        },
        methods: {
            toHome() {
                location.href = '/#!/'
            },
            logout() {
                unset('user', '/', location.hostname)
                location.href = '/#!/login'
            }
        },
        computed: {
            time(){
                let hours = new Date().getHours()
                if (hours > 5 && hours < 12) {
                    return ''
                } else if (hours > 11 && hours < 19) {
                    return ''
                } else {
                    return ''
                }
            }
        }
    }
</script>
<style lang="sass">
    @import "../style/components/NaviHeader.scss";
</style>


