<template>
    <section class="editor">
        <input class="title"
               placeholder=""
               v-model="title">
        <div :class="view">
            <textarea v-model="input">
            </textarea>
            <button class="toggle"
                    @click="editToggle">
                <i class="icon iconfont icon-jiantou-left"
                   v-show="view==='edit'">
                </i>
                <i class="icon iconfont icon-jiantou-right"
                   v-show="view==='inspect'">
                </i>
            </button>
            <article v-html="input | marked">
            </article>
        </div>
        <div class="panel">
            <input type="text"
                   v-model="dateStr"
            >
            <button class="save"
                    @click="send">
            </button>
        </div>
    </section>
</template>
<script>
    import marked       from '../js/marked.min'
    import hljs         from '../js/highlight.min'
    import {pop}        from '../vuex/actions'
    export default{
        data(){
            return {
                title: '',
                input: '',
                date: '',
                id: '',
                view: 'edit'
            }
        },
        filters: {
            marked
        },
        watch:{
            input:function () {
                hljs.initHighlighting()
                hljs.initHighlighting.called = false
            }
        },
        created(){
            if (this.$route.query.id) {
                let id = this.$route.query.id
                this.$http.get('/web/console/article?id=' + id)
                    .then((response)=> {
                        let res = JSON.parse(response.body)
                        let code = res.retcode
                        let data = res.data
                        switch (code){
                            case 200:
                                this.input = data.article.content
                                this.title = data.article.title
                                this.date = data.article.date
                                this.id = data.article._id
                                break
                            case 410:
                                alert('')
                                break
                            case 400:
                                this.pop({
                                    pop: true,
                                    content: desc,
                                    btn1: '',
                                    cb1: ()=>{
                                        window.history.back(-1); 
                                    }
                                })
                                break
                        }
                    })
            } else {
                this.date = new Date().toLocaleDateString()
            }
        },

        methods: {
            send(){
                this.title = this.title.trim()
                if (!this.title) {
                    alert('')
                    return
                }
                if (!this.dateStr.trim()) {
                    this.date = new Date()
                }

                this.$http.post('/web/save', this.$data)
                        .then((response)=> {
                            let res = JSON.parse(response.body)
                            let code = res.retcode
                            let data = res.data
                            switch (code){
                                case 200:
                                    this.pop({
                                        pop: true,
                                        content: '',
                                        cb1: function () {
                                            this.pop({})
                                            this.$router.go('/console/articleList')
                                        }.bind(this)
                                    })
                                    break
                                case 410:
                                    alert('')
                                    break
                            }
                        }, (response)=> {
                            console.log(response)
                        })
            },
            editToggle(){
                this.view = this.view === 'edit' ? 'inspect' : 'edit'
            },
        },
        computed: {
            dateStr: {
                set(value){
                    value = value.trim()
                    let reg = /(\d{4})(\d+)(\d+)/
                    if (reg.test(value)) {
                        let date = RegExp.$1
                                + '/' + RegExp.$2
                                + '/' + RegExp.$3
                        this.date = new Date(date)

                    } else {
                        this.date = new Date()
                    }
                },

                get(){
                    let d = new Date(this.date)
                    if (d != 'Invalid Date') {
                        return d.getFullYear() + '' +
                                (d.getMonth() + 1) + '' +
                                d.getDate() + ''
                    } else {
                        return ''
                    }
                }
            }
        },
        ready(){
            hljs.initHighlighting()
            hljs.initHighlighting.called = false
        },
        vuex: {
            actions: {
                pop
            }
        }
    }
</script>
<style lang="sass">
    @import "../style/components/Editor.scss";
</style>


