                                window.history.back(-1); 
                            }
                        })
                }
            }, (response)=> {
                console.log(response)
            })
        },
        methods:{
            goHome(){
                if(this.loginUserName){
                    location.href = '/' + this.loginUserName + '#!/'
                }else{
                    this.$router.go('/')
                }
            }
        }

    }
</script>
