<template>
    <section class="articleList">
        <button @click="$router.go('/console/editor')">
            
        </button>
        <table>
            <tbody>
                <tr>
                    <th @click="sort('title')"></th>
                    <th @click="sort('date')"></th>
                    <th></th>
                </tr>
                <tr v-for="article in articles">
                    <td @click="detail(article._id)">
                        {{article.title}}
                    </td>
                    <td>
                        {{article.date | dateParse}}
                    </td>
                    <td>
                        <i class="icon iconfont icon-xiugai"
                           @click="edit(article._id)">
                        </i>
                        <i class="icon iconfont icon-shanchu"
                           @click="deleteItem(article._id,$index)">
                        </i>
                    </td>
                </tr>
            </tbody>
        </table>
    </section>
</template>
<script>
    import {pop}        from '../vuex/actions'
    export default{
        data(){
            return {
                articles: null,
                sortState:{
                    date:0,
                }

            }
        },
        filters: {
            dateParse: value=>{
                let d = new Date(value)
                return d.getFullYear() + '' +
                        (d.getMonth()+1) + '' +
                        d.getDate() + ''
            }
        },
        created(){
            this.$http.get('/web/console/articleList')
                .then((response)=> {
                    let res = JSON.parse(response.body)
                    let code = res.retcode
                    let data = res.data
                    switch (code){
                        case 200:
                            this.articles = data.articles.sort((i, j)=> {
                                return new Date(j.date) - new Date(i.date)
                            })
                            break
                        case 410:
                            alert('')
                            break
                    }
                }, (response)=> {
                    console.log(response)
                })
        },
        methods:{
            edit(id){
                this.$router.go('/console/editor?id='+id)
            },
            deleteItem(id,index){
                this.pop({
                    pop:true,
                    content:'',
                    cb1:function () {
                        this.$http.post('/web/delete',{id})
                            .then((response)=> {
                                let res = JSON.parse(response.body)
                                let code = res.retcode
                                let data = res.data
                                switch (code){
                                    case 200:
                                        this.pop({
                                            pop:true,
                                            content:'',
                                            cb1:function () {
                                                this.pop({})
                                                this.articles.splice(index,1)
                                            }.bind(this)
                                        })
                                        break
                                    case 410:
                                        alert('')
                                        break
                                }
                            }, (response)=> {
                                console.log(response)
                            })
                    }.bind(this),
                    btn2: ''
                })

            },
            detail(id){
                this.$router.go('/article?id='+id)
            },
            sort(opt){
                if(opt=='date'){
                    if(this.sortState.date===0){
                        this.articles = this.articles.sort((i, j)=>{
                            return new Date(i.date) - new Date(j.date)
                        })
                        this.sortState.date=1
                    }else {
                        this.articles.reverse()
                    }
                }else if(opt=='title'){
                    this.articles.reverse()
                }
            }
        },
        vuex: {
            actions:{
                pop,
            }
        }
    }
</script>
<style lang="sass">
    @import "../style/components/ArticleList.scss";
</style>


